import React, { PureComponent } from "react";

import axios from 'axios';
import moment from 'moment-timezone';
import { Link } from 'react-router-dom';
import {
    Row, Col, Avatar,
    Divider, List, Icon,
} from 'antd';
import API_KEY from '../../utils/apiKeys';

const USERNAME = 'marllonfrizzo';

export default class musica extends PureComponent {

    constructor(props) {
        super(props);

        this.state = {
            info: null,
            tracks: [],
            topArtists: [],
        };
    }

    componentDidMount() {
        this._requestInfo();
        this._requestRecentTracks();
        this._requestTopArtists();
    }

    _requestTopArtists = () => {
        this.setState({ loading: true });
        return axios.get(`https://ws.audioscrobbler.com/2.0/?method=user.gettopartists&user=${USERNAME}&api_key=${API_KEY}&format=json&limit=5&period=7day`)
            .then(response => {
                const { data } = response;
                this.setState({
                    topArtists: data.topartists.artist,
                })
            })
            .catch(ex => {
                console.error(ex);
            })
            .finally(() => {
                this.setState({ loading: false });
            })
    }

    _requestRecentTracks = () => {
        this.setState({ loading: true });
        return axios.get(`https://ws.audioscrobbler.com/2.0/?limit=5&method=user.getrecenttracks&user=${USERNAME}&api_key=${API_KEY}&format=json&extended=1`)
            .then(response => {
                const { data } = response;
                this.setState({
                    tracks: data.recenttracks.track,
                })
            })
            .catch(ex => {
                console.error(ex);
            })
            .finally(() => {
                this.setState({ loading: false });
            })
    }

    _requestInfo = () => {
        this.setState({ loading: true });
        return axios.get(`https://ws.audioscrobbler.com/2.0/?method=user.getinfo&user=${USERNAME}&api_key=${API_KEY}&format=json`)
            .then(response => {
                const { data } = response;
                this.setState({
                    info: data.user,
                })
            })
            .catch(ex => {
                console.error(ex);
            })
            .finally(() => {
                this.setState({ loading: false });
            })
    }

    _renderInfo = () => {
        const { info } = this.state;

        if (!info) return null;

        const {
            name, realname, country, url,
            registered, playcount, image,
        } = info;

        const date = moment.unix(registered.unixtime);

        return (
            <div>
                <h3>
                    <span>{realname} ({name}) <a href={url} target="_blank" rel="noopener noreferrer">
                        <Icon type="link" theme="outlined" />
                    </a>
                    </span>
                </h3>
                <Row >
                    <Col xs={6} sm={3} lg={2}>
                        <Avatar shape="square" size={64} src={image[1]['#text']} />
                    </Col>
                    <Col xs={18} lg={8}>
                        Usuário desde <strong>{date.locale('pt-br').format('LL')}</strong><br />
                        Total de reproduções: <strong>{playcount}</strong><br />
                        País: <strong>{country}</strong><br />
                    </Col>
                </Row>
            </div>
        )
    }

    _formatItemList = (artist, album) => (
        <span>Por <strong>{artist}</strong>, do álbum <strong>{album}</strong></span>
    )

    _renderRecentTracks = () => {
        const { tracks, loading } = this.state;

        if (!tracks) return null;

        let day;

        return (
            <List
                itemLayout="horizontal"
                loading={loading}
                pagination={false}
                dataSource={tracks}
                renderItem={item => (
                    <List.Item>
                        <List.Item.Meta
                            avatar={<Avatar src={item.image[3]['#text']} />}
                            title={
                                <span>
                                    <span>{item.name} </span>
                                    {item.loved === '1'
                                        ? (
                                            <Icon
                                                type="heart"
                                                theme="filled"
                                                style={{
                                                    color: 'red'
                                                }}
                                            />
                                        )
                                        : null
                                    }
                                </span>
                            }
                            description={this._formatItemList(item.artist.name, item.album['#text'])}
                        />
                        {item['@attr']
                            ? 'Reproduzindo agora'
                            : moment.unix(item.date.uts).locale('pt-br').format('lll')
                        }
                    </List.Item>
                )}
            />
        );
    }

    _renderTopArtists = () => {
        const { topArtists, loading } = this.state;

        if (!topArtists) return null;

        return (
            <List
                itemLayout="horizontal"
                loading={loading}
                pagination={false}
                dataSource={topArtists}
                renderItem={item => (
                    <List.Item>
                        <List.Item.Meta
                            avatar={<Avatar src={item.image[3]['#text']} />}
                            title={item.name}
                        />
                        <span><strong>{item.playcount}</strong> reproduções</span>
                    </List.Item>
                )}
            />
        );
    }

    render() {
        return (
            <div>
                <h1>Minhas músicas</h1>
                {this._renderInfo()}
                <Divider>Últimas 5 músicas reproduzidas</Divider>
                {this._renderRecentTracks()}
                <Divider>Top 5 artistas da última semana</Divider>
                {this._renderTopArtists()}
                <div style={{ marginTop: '35px' }}>
                    <span>Voltar ao <Link to="/">início</Link>.</span>
                </div>
            </div>
        );
    }

};



